<template>
  <div
    v-click-outside="onClickOutside"
    class="dropdown"
    :class="{'is-active' : advancedMenu || valid == false}"
    style="width: fit-content;"
  >
    <div
      id="advanced_options_control"
      class="dropdown-trigger"
      @click="advancedMenu = !advancedMenu"
    >
      <button
        class="button is-flex is-justify-content-space-between"
        :class="{'is-danger' : valid === false}"
        type="button"
        aria-haspopup="true"
        :aria-controls="'dropdown_'+id"
      >
        <span>{{ text }}</span>
        <span class="icon">
          <i class="fas fa-chevron-down" />
        </span>
      </button>
    </div>
    <div
      :id="'dropdown_'+id"
      class="dropdown-menu"
      style="width: 300px;"
      :style="alignment ? 'left: -' + alignment + 'px;' : 'left: -60px;'"
    >
      <div
        class="dropdown-content"
        style="max-height: 50vh; overflow-y: auto;"
      >
        <slot />
      </div>
    </div>
  </div>
</template>

<script>
  import vClickOutside from 'click-outside-vue3'
  export default {
    name: "Dropdown",
    directives: {
      clickOutside: vClickOutside.directive
    },
    props : ["text", "valid", "alignment"],
    data () {
      return {
        advancedMenu : false,
        id : this.$store.getters.getUid
      }
    },
    methods : {
      onClickOutside() {
        this.advancedMenu = false;
      }
    }
  };
</script>